/* src/components/Header.css */
.header {
  background-color: #333;
  padding: 20px;
  text-align: center;
}

.header nav ul {
  list-style-type: none;
  padding: 0;
}

.header nav ul li {
  display: inline;
  margin-right: 20px;
}

.header nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.header nav ul li a:hover {
  text-decoration: underline;
}
